@font-face {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  src: url("FontFont_FF.Mark.Pro.9b0d5f6c.otf") format("otf");
  font-weight: normal;
  font-style: normal;
}

html {
  scroll-behavior: smooth;
}

body {
  scroll-behavior: smooth;
  background: linear-gradient(to right, #191536, #000);
  margin: 0;
  font-family: Inter, sans-serif;
  font-size: 16px;
  font-weight: 500;
}

.page__aside {
  opacity: 0;
  pointer-events: none;
  transition: opacity .3s, background-color .3s, transform .3s .3s;
  transform: translateX(-120%);
}

.page__aside--active {
  pointer-events: all;
  opacity: 1;
  background-color: #00000080;
  transition: transform .3s, background-color .3s .3s;
  transform: translateX(0);
}

.page__no-scroll {
  overflow: hidden;
}

.section {
  padding-block: 50px;
  padding-inline: 20px;
}

@media (width >= 640px) {
  .section {
    padding-block: 70px;
  }
}

@media (width >= 1280px) {
  .section {
    padding-block: 100px;
  }
}

@media (width >= 640px) {
  .section {
    padding-inline: 34px;
  }
}

@media (width >= 1280px) {
