.dropdown {
  cursor: pointer;
}

.dropdown__title {
  color: #05c2df;
  align-items: baseline;
  gap: 3px;
  display: flex;
}

.dropdown__title:hover {
  color: #fff;
}

.dropdown__title--text {
  color: inherit;
  font-size: 16px;
  line-height: 20.8px;
  transition: color, all .3s;
}

.dropdown__title--triangle {
  border-top: 4px solid #05c2df;
  border-left: 4px solid #0000;
  border-right: 4px solid #0000;
  width: 0;
  height: 0;
  transition: border-top-color, all .3s;
}

.dropdown__content {
  z-index: 3;
  opacity: 0;
  pointer-events: none;
  background-color: #191536;
  border: 1px solid #05c2df;
  width: max-content;
  transition: transform .3s, opacity .3s;
  position: absolute;
  top: 85px;
  overflow: hidden;
  transform: translateY(-25px);
}

.dropdown__content--active {
  pointer-events: auto;
  opacity: 1;
  transform: translateY(5px);
}

.dropdown__list {
  flex-direction: column;
  padding: 0;
  display: flex;
}

.dropdown__item {
  cursor: pointer;
  color: #fff;
  padding: 5px 20px;
  transition: background-color .3s;
}

.dropdown__item:hover {
  background-color: #110e25;
}

.dropdown__item--active {
  color: #05c2df;
}

.dropdown__title:hover .dropdown__title--text {
  color: #fff;
}

.dropdown__title:hover .dropdown__title--triangle {
  border-top-color: #fff;
}

.icon {
  background-image: url("burger-menu.059cd882.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 20px;
  height: 20px;
  transition: transform .3s;
}

.icon:hover {
  transform: scale(1.2);
}

.icon--fb {
  background-image: url("footer-social-1.2a52e41d.svg");
}

.icon--tw {
  background-image: url("footer-social-2.23bc9b05.svg");
}

.icon--yt {
  background-image: url("footer-social-3.66a09fee.svg");
}

.icon--rd {
  background-image: url("footer-social-4.1ea4690f.svg");
}

.icon--close {
  cursor: pointer;
  background-image: url("close.06b09b6f.svg");
  background-color: inherit;
  border: none;
}

.nav {
