.slider {
  gap: 103px;
  width: 207px;
  padding-bottom: 10px;
  display: flex;
  position: relative;
}

.slider__left {
  color: #929292;
  cursor: pointer;
  transition: color .3s;
}

.slider__right {
  color: #fff;
  cursor: pointer;
  transition: color .3s;
}

.slider p {
  margin: auto auto 7px;
}

.slider :before {
  content: "";
  background-color: #212121;
  width: 100%;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.slider :after {
  content: "";
  width: 33.3%;
  height: 2px;
  left: var(--slider-left, 0%);
  background-color: #05c2df;
  transition: left .3s;
  position: absolute;
  bottom: 0;
}

